.main-body{
    padding: 5vh 10vw;   
}
#person-page-list::-webkit-scrollbar-track{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	border-radius: 5px;
	background-color: #F5F5F5;
}

#person-page-list::-webkit-scrollbar{
	width: 17px;
	background-color: #B88D30;
} 
#person-page-list::-webkit-scrollbar-thumb{
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	background-color: #B88D30;
} 

#person-page-list{
    height: 70vh;
    overflow: scroll;
    overflow-x: hidden;
    width: 70%;
    margin: 30px 0px;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

#person-page-list div{
    display: flex;
    align-items: center;
    gap: 18px;
}
#person-page-list div input{
    text-transform: capitalize;
    font-weight: 500;
    background-color: #b88d303c; 
    padding: 7px 10px;
    font-size: 1.1em;
    border-radius: 0.4em;
    color: black;
    border: 3px solid #b88d303c;
}
#person-page-list div aside{
    cursor: pointer;
}
.input-tobe-change{
    border: 3px solid black!important;
}
.input-tobe-not-to-change{
    color: rgb(99, 90, 90)!important;
    background-color: #b88d3021!important;
}
#person-page-list div p{
    font-weight: 400;
    background-color: black;
    color: white;
    border:2px solid black;
    padding: 1px 7px;
    border-radius: 0.2em;
    font-size: 0.9em;
}
#person-page-list div p:hover{
    background-color: white;
    color: black;
    cursor: pointer;
}




























@media only screen and (max-width: 768px) {

    .main-body{
        padding: 2vh 4vw;   
    } 
    
    #person-page-list{
        height: 100vh;
        width: 100%;
        overflow: auto;  
    }
    
    #person-page-list div{
        display: flex;
        align-items: center;
        gap: 18px;
    }
    #person-page-list div input{
        width: 70%; 
    } 

}