#sm-header {
    display: flex;
    align-items: center;
}
#sm-header > h1{
    font-weight: 900;
    font-size: 3em;
    flex: 0.8;
    text-align: center;
}
#sm-header div{
    display: flex;
    gap: 40px;
    font-size: 2em;
    align-items: center;   
    background-color: rgb(233, 229, 229);
    display: inline-flex;
    padding: 7px 25px 10px 15px;
    border-radius: 0.2em;
    cursor: pointer;
    transition: color 0.3s linear;
}
#sm-header div:hover{
    background-color: black;
    color: white;
}
#sm-header div > p{
    font-weight: 500;
}



@media only screen and (max-width: 768px) {
    #sm-header > h1{
        font-size: 2em;
    }
    #sm-header div{
        gap: 20px;
        font-size: 1.4em;
    }
}