#modal-add{
    background-color: white; 
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 50px 100px;
    border: 6px solid #000;
}
#modal-add{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 20px;
    border-radius: 1em;
}
#modal-add > aside{
    position: absolute;
    right: 10px;
    top: 10px;
    zoom: 2;
    cursor: pointer;
}
#modal-add > p{ 
    margin-top: 60px;
    width: 100%;
    font-weight: 400;
    color: rgb(99, 0, 0);
    
}
#modal-add input{
    border: 2px solid #000;
    border-radius: 1em;
    padding: 8px 20px;
    font-size: 1.5em;
    font-weight: 400;
}
#modal-add button{
    width: 40%;
    background-color: rgb(79, 66, 66);
    color: white;
    font-weight: 500;
    border: none;
    box-shadow: 0px 0px 10px rgb(151, 145, 145) ;
    padding: 7px 30px 10px;
    border-radius: 10em;
    font-size: 1.5em;
    cursor: pointer;
}
.modal-add-div{
    position: fixed;
    bottom: 0;
    right: 0;
    background-color: #B88D30;
    border-top-left-radius: 100em;
    width: 200px;
    height: 180px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 4px;
    color: white;
    box-shadow: 0px 0px 10px rgb(148, 141, 141);
    cursor: pointer;
    transition: all 0.8s linear;
} 
.modal-add-div:hover{
    box-shadow: 0px 0px 100px rgb(148, 141, 141);
}
.modal-add-div p{
    font-weight: 400;
    font-size: 2em;transform: translate(20px,15px);
    font-size: 2em;
}



















@media only screen and (max-width: 768px) {
    #modal-add{ 
        padding: 50px 10px;
        border: 4px solid #000;
    }
    #modal-add{ 
        gap: 30px; 
    } 
    #modal-add > p{ 
        margin-top: 60px;
        width: 100%;
        font-weight: 400;
        color: rgb(99, 0, 0); 
    }
    #modal-add input{ 
        padding: 8px 14px;
        font-size: 1em; 
    }
    #modal-add button{ 
        font-size: 1em; 
    }



    
    .modal-add-div{
        position: fixed;
        bottom: 0;
        right: 0;
