#message-alert{
    position: absolute;
    z-index: 100;
    right:  0px;
    top: 40px;
} 
#message-alert p{ 
    background-color: #B88D30;
    color: white;
    font-weight: 700;
    font-size: 2em;
    padding: 5px 24px 8px;
    border-top-left-radius: 100px;
    border-bottom-left-radius: 100px;
      animation: left-to-right 2s ease forwards;  
}
@keyframes left-to-right{
    0%{
        opacity: 0; 
    }
    50%{
        opacity: 1; 
    }
    100%{
        opacity: 0; 
    }
}