#home{
    height: 100vh;
    display: flex;
    justify-content: center;
    flex-direction: column;
    gap: 10vh;
    align-items: center;
}
#home > h1{
    font-weight: 900;
    font-size: 3em;
}
#home section{
    display: flex;
    gap: 50px;
}
#home section p{
    font-size: 1.6em;
    background-color: #B88D30;
    color: white;
    padding: 8px 25px 10px;
    border-radius: 50em;
    box-shadow: 0px 0px 20px rgb(177, 167, 167);
    cursor: pointer;
    position: relative;
    transition: transform 0.7s linear;
}
#home section p:hover{
    transform: scale(1.04);
}
#home section p:hover:after{
    content: "";
    position: absolute; 
    top: 0;
    border-radius: 50em;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #B88D30; 
    animation: fade-up 0.4s linear forwards; 
}
#home section h6{
    text-align: center;
    margin-top: 10px;
    font-weight: 400;
    font-size: 0.6em;
}
@keyframes fade-up{
    0%{
       transform: scale(0);
    }
    50%{
        transform: scale(0.4);
        opacity: 0.4;
    }
    100%{transform: scale(1);
        opacity: 0;
    }
}














@media only screen and (max-width: 768px) {
    #home{ 
        text-align: center; 
    }
    #home > h1{
        padding: 0px 20px;
    }
    #home section{
        display: block;
    }
    #home section p{
        margin-top: 50px;
    }  
}